export default {
    props:{
		loading:{
			type: [String,Number],
			default: ''
		}
	},
    data(){
        return{
            loadmore:[{
					id: 0,text:'上拉加载更多'
				},{
					id: 1,text: '正在努力加载中....'
				},{
					id: 2,text: '已经没有更多了'
				}]
        }
    }
}
